import { useState,useEffect } from "react";


function ExUseEffect(props){
  let [count, setCount] = useState(0)
  let [boxDispLay, setBoxDispLay] = useState(true)
  let [num, setNum] = useState("")

  //마운트 업데이트 될 때 실행, HTML이 먼저 로딩되고 실행됨
  useEffect(()=>{
    console.log('hello')
    let timer1 = setTimeout(()=>{setBoxDispLay(false)},2000)
    if(isNaN(num) == true){
      setNum("")
      alert("숫자만입력해주세요")
      document.getElementById("numInput").value=""
      
    }
      

    //useEffect보다 먼저 실행(clean up function)
    //타이머제거, socket 연결요청제거, ajax요청 중단 이런 코드를 많이 작성
    //컴포넌트 unmount 시에도 clean up function 안에 있던게 1회 실행
    return ()=>{
      clearTimeout(timer1)
    }
  },[num]) // [] 안의 변수가 변할 때 실행, 마운트 될 때 실행
  //[] 인 경우 업데이트 될 때 실행 안됨
  
  
    return(
      <div className="container">
        {
        boxDispLay ?  
        <div className="alert alert-warning"></div> : null
        }
        <div>{count} <button onClick={ ()=> {setCount(count+1)}}>button</button></div>
        <div><input id="numInput" onChange={ (e)=> setNum(e.target.value) }></input></div>
        { num }
    </div> 
    )
}

export default ExUseEffect;